 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-09 10:39:35
 -->

<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">类别维护</div>
      <!--  @click="$router.push({name: 'PersonList'})" -->
      <div class="head-btn"
        @click="addevent"
        v-if="btns.hasInsert"
        data-btn-id='106008001'>
        <img src="@/assets/img/add.png"
          alt="登记">&nbsp;<span>新增</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-form-item label='类别名称：'>
          <el-input placeholder="请输入"
            v-model="queryObj.deptTypeName"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='所属机构名称：'>
          <el-select v-model="queryObj.createOrganizationId"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="$refs.pagination.initPageList(1)">查询</el-button>
        </el-form-item>

        <el-form-item label="显示状态："
          style="float:right;margin-right:0">
          <el-radio-group class="red"
            v-model="queryObj.enabled"
            @change="$refs.pagination.initPageList(1)">
            <el-radio label="">全部</el-radio>
            <el-radio label="1">已启用</el-radio>
            <el-radio label="2">已禁用</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label='机构名称'
          prop="oname"
          show-overflow-tooltip></el-table-column>
        <el-table-column label='类别顺序'
          prop='sortNum'
          width='100'>
        </el-table-column>
        <el-table-column label='类别编号'
          prop='sysGuid'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='类别名称'
          prop='deptTypeName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='类别状态'
          width="100">
          <template v-slot="{row}">
            <span :class="[row.enabled == 1 ? 'status-enable' : 'status-disable']"></span>
          </template>
        </el-table-column>
        <el-table-column label='创建人员'
          prop='createUserName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='创建日期'
          width="120">
          <template v-slot="{row}">
            {{row.createDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label='操作'>
          <template v-slot="{row}">
            <i class="icon iconfont bj-search"
              @click="detaill(row)"></i>
            <i class="icon iconfont bj-edit"
              @click="edit(row)"
              v-if="btns.hasUpdate&&row.enabled==1"></i>
            <i class="icon iconfont bj-delete"
              @click="delConfirm(row.sysGuid)"
              v-if="btns.hasDel&&row.enabled==1"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination queryUrl='/sys/deptType/listPaging'
        :queryObj='queryObj'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref="pagination" />
    </div>
    <el-dialog :visible.sync="dialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='500px'
      @close='onDialogClose'
      :title="dialogTitles"
      :close-on-click-modal='false'
      center>
      <template>
        <el-form size="small"
          label-width="auto"
          :model="formFields"
          :rules="rules"
          ref="form"
          style="padding:0 50px">
          <!-- <el-form-item label="类别编号：">
            <template>{{sysGuid}}</template>
          </el-form-item> -->
          <el-form-item label="类别顺序："
            prop="sortNum">
            <el-input placeholder="请输入"
              :value="formFields.sortNum"
              @input="$outInt($event, 'sortNum', formFields)"
              maxlength="5"></el-input>
          </el-form-item>
          <el-form-item label="类别名称："
            prop="deptTypeName">
            <el-input placeholder="请输入"
              v-model.trim="formFields.deptTypeName"
              maxlength="51"></el-input>
          </el-form-item>
          <el-form-item label="是否启用："
            prop="enabled">
            <el-select class="block"
              v-model="formFields.enabled">
              <el-option value="1"
                label="启用"></el-option>
              <el-option value="2"
                label="禁用"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot='footer'>
          <el-button class="btn-hollowing"
            @click="dialogIsShow = false">取消</el-button>
          <el-button class="btn-info"
            @click="onSubmit"
            :loading="submitLoading">保存</el-button>
        </div>
      </template>
    </el-dialog>
    <!-- 查看详情 -->
    <el-dialog :visible.sync="lookdialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='500px'
      @close='lookonDialogClose'
      title="查看"
      :close-on-click-modal='false'
      center>
      <div class="flex">
        <el-form size="small"
          class="see-style"
          label-width="auto">
          <el-form-item label="类别编号：">{{sysGuid}}</el-form-item>
          <el-form-item label="类别顺序：">{{detaillistData.sortNum}}</el-form-item>
          <el-form-item label="类别名称：">
            {{detaillistData.deptTypeName}}
          </el-form-item>
          <el-form-item label="是否启用：">
            {{detaillistData.enabled==1?'是':'否'}}
          </el-form-item>
        </el-form>
      </div>
      <!--  v-if="detaillistData.enabled==1" -->
      <div slot='footer'
        v-if='btns.hasUpdate'>
        <el-button class="btn-info"
          @click="submitEdit">修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { lenRule } from '@/lib/utils'
import { mapState } from 'vuex'
export default {
  name: 'CategoryList',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      dialogIsShow: false,
      lookdialogIsShow: false,
      dialogTitles: '新增',
      tableData: [],
      queryObj: {
        deptTypeName: '',
        enabled: '',
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,
      formFields: {
        sortNum: '',
        deptTypeName: '',
        enabled: ''
      },
      sysGuid: '-',
      detaillistData: [],
      rules: {
        sortNum: { required: true, message: '请输入', trigger: 'blur' },
        deptTypeName: [
          {
            required: true,
            message: '请输入',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        enabled: { required: true, message: '请选择' }
      },
      loading: true,
      submitLoading: false
    }
  },
  methods: {
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },
    delConfirm(sysGuid) {
      this.$confirm('', {
        title: '确定删除记录？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/sys/deptType/remove', { sysGuid })
            .then(res => {
              this.$message.success('删除成功')
              if (this.tableData.length === 1) {
                let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
                this.$refs.pagination.initPageList(pageNum)
              } else {
                this.$refs.pagination.initPageList()
              }
            })
            .catch(err => {
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    },
    onDialogClose() {
      this.dialogIsShow = false
    },
    lookonDialogClose() {
      this.lookdialogIsShow = false
    },
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.submitLoading = true
        let url, params
        if (this.dialogTitles == '新增') {
          url = '/sys/deptType/insert'
          params = { ...this.formFields }
        } else {
          url = '/sys/deptType/update'
          params = { ...this.formFields, sysGuid: this.sysGuid }
        }
        this.$post(url, params)
          .then(res => {
            this.submitLoading = false
            this.$message.success(`${this.dialogTitles}成功`)
            this.$refs.pagination.initPageList()
            this.dialogIsShow = false
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },
    addevent() {
      this.dialogIsShow = true
      this.dialogTitles = '新增'
      this.formFields = {
        sortNum: '',
        deptTypeName: '',
        enabled: '1' // 默认值：1为启用
      }
      this.$nextTick(_ => {
        this.$refs.form.clearValidate()
      })
    },
    edit(row) {
      this.dialogIsShow = true
      this.dialogTitles = '修改'
      this.formFields.sortNum = row.sortNum
      this.formFields.deptTypeName = row.deptTypeName
      this.formFields.enabled = row.enabled
      this.sysGuid = row.sysGuid
      this.$nextTick(_ => {
        this.$refs.form.clearValidate()
      })
    },
    detaill(row) {
      this.lookdialogIsShow = true
      this.detaillistData = row
      this.sysGuid = row.sysGuid
    },
    submitEdit() {
      this.edit(this.detaillistData)
      this.lookdialogIsShow = false
    }
  },
  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('106008001'), // 新增按钮权限
        hasUpdate: ~btnIds.indexOf('106008002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('106008003'), // 删除按钮权限
        hasStatus: ~btnIds.indexOf('106008004') // 更改状态权限
      }
    },
    ...mapState(['orgList'])
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}

.table-box {
  padding: 0 20px;
  overflow: hidden;
}
.see-style {
  margin: 0 auto;
  max-width: 100%;
}

